
.card[data-card-height="cover-full"],
.card[data-card-height="cover"] {
  height: 100vh;
}

.card[data-card-height="cover-card"] {
  height: calc(100vh - 200px);
}

#form1a-select {
  border-color: rgba(255, 255, 255, 0.1) !important;
}

.custom_play_btn {
  background: url(data:image/png;base64,);
  background-color: transparent !important;
  background-size: 100% 100%;
  border: 0;
  width: 100px;
  height: 30px;
  border-radius: 15px;
  padding: 3px 5px;
  margin: 8px 7px 12px 7px;
  display: flex;
  justify-content: center;
  /* border: 2px solid #e9ac20; */
  color: white;
  box-shadow: 0 1px 3px rgb(0, 0, 0, 0.5);
}



.balance_wrapper {
  font-size: 13px;
  font-family: inherit;
  padding: 0 10px 15px;
  height: 170px;
}

.home_balance {
  overflow: hidden;
}

.links {
  text-align: center;
  margin: 0px;
}

.links a {
  width: calc(50% - 5px);
  background-image: url(/diamond-addy/static/media/login.5829eb34.jpg);
  background-size: 100% 100%;
  color: transparent;
  text-shadow: none;
  height: 37px;
  margin: 10px 0px 5px;
  max-width: 200px;
  display: inline-block;
  padding: 5px 15px;
  border-radius: 2px;
}

.register {
  background-image: url(/diamond-addy/static/media/register.1e988f52.jpg) !important;
  margin-left: 10px !important;
}

.pbalance {

  overflow: hidden;
}

.balance_btns {
  float: right;
  width: 135px;
  padding: 0 0 5px 5px;
  display: flex;
  flex-direction: column;
}

.balance_btns .btns {
  background: url(/diamond-addy/static/media/deposit.544a920d.png);
  background-size: 100% 100% !important;
  border: 0;
  color: transparent;
  text-shadow: none;
  border-radius: 0;
  box-shadow: none;
  margin-top: 3px;
  text-align: left;
  padding: 5px;

}

.balance_btns .withdraw_btn {
  background: url(/diamond-addy/static/media/withdraw.ae9f8ea4.png);
}

.balance_btns .refresh_btn {
  background: url(/diamond-addy/static/media/refresh.1893ef65.png);
}

.balance_btns .btns i {
  margin: 4px -23px;
  position: absolute;
  font-size: 18px;
}
.page-title .logo{
  height: 46px;
  width: 100%;
  margin: 5px 10px;
}
.currency_icon img{
  height: 32px;
  margin: 10px !important;
}
.btn_green{
  color: #FFC107 !important;
  background-image:linear-gradient(to bottom,#22492B,#1BD144) ;
  border-radius: 10px !important;
  border: 0;
  box-shadow: 0 1px 3px rgb(0, 0, 0, 0.5);
  font-size: 15px !important;
  line-height: 20px !important;
  padding: 8px 0;
}
.register_btn{
  width: 150px;
  background-image:linear-gradient(to bottom,#3093c7,#1c5a85) !important;
  background: #3093c7;
  border-color: #25729a
}
.badge_anima{
  background: #f38120;
  margin: 3px -40px;
  position: absolute !important;
  color: #fff !important;
  line-height: 20px;
  width: 20px;
  text-align: center;
  border-radius: 10px;
  top: 5px;
  right: 50%;
  font-weight: 700 !important;
  font-size: 12px !important;
  opacity: 1 !important;
}
.main_pulse{
  -webkit-animation: pulse 1s infinite ease-in-out alternate;
          animation: pulse 1s infinite ease-in-out alternate;
}
marquee{
  background-image: url(/diamond-addy/static/media/marquee.a4c88ad7.jpg);
  font-size: 11px;
  padding: 7px;
  color: #A9A9A9;
  letter-spacing: 0.5px;
  height: 20px;
  box-shadow: 0 1px 3px rgba(0,0,0,1);
  line-height: 1;
  background-size: 100% 100%;
  z-index: 2;
  position: relative;
  overflow: hidden;
}
#settings{
  padding: 10px;
  font-size: 18px !important;
  font-weight: 700 !important;

}
.setting_head{
  color: #FFC107 !important;
  border-bottom: 2px solid;
  padding-bottom: 10px !important;
}
#settings a{
  padding: 15px 0 !important;
  border-bottom: 1px solid;
  color: white;
  display: block;
  font-size: 15px;
}
.color_yellow{
  color: #FFC107 !important;
}
#deposit_cards{
  padding: 15px;
  overflow: hidden;
}
#deposit_cards .pay_menu{
  background: transparent;
  display: block;
  padding: 7px;
  border-radius: 4px;
}
#deposit_cards .pay_menu img{
  width: 100% ;
}
